// Empresa.js
import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import API from '../../utils/API';
import { Space, Form, Input, Button, Typography, Divider, Select } from 'antd';
const { Title } = Typography;
const { Option } = Select;

const Empresa = (props) => {
  const [empresa, setEmpresa] = useState(0)
  const [empleados, setEmpleados] = useState(0)

  const history = useHistory();

  useEffect(() => {
    loadEmpresa()
  })
  
  const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };
  
  const tailLayout = {
    wrapperCol: { offset: 8, span: 16 },
  };
  
  const titleStyle = {
    marginTop: 20,
  }

  const loadEmpresa = () => {
    if(props.match.params.id !== '0'){
      API.getEmpresa(props.match.params.id)
        .then(({data}) => {
          setEmpresa(data)
          form.setFieldsValue(data)
        })
        .catch(console.log);
    }
  }

  const [form] = Form.useForm();

  const onFinish = ({nombre, responsable}) => {
    if (nombre) {
      if(empresa._id){
        setEmpresa({nombre, responsable})
        API.updateEmpresa(empresa)
          .then(history.push('/empresas'))
          .catch(console.log);
      }else{
        API.saveEmpresa({
          nombre,
          responsable,
          fechaAlta: Date.now().toString()
        })
          .then(history.push('/empresas'))
          .catch(err => console.log(err));
      }
    }
    console.log('Success:', {nombre, responsable});
  };
  
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  const valoresInicio = () => {
    if(empresa._id){
      form.setFieldsValue({...empresa})
    }else{
      form.resetFields()
    }
  }

  const cargarSelect = () => {
    if(!empleados){
      var retorno = []
      API.getEmpleados().then(
        ({data}) => {
        setEmpleados(data)
        if(data){
          data.map(({_id, nombre}) => (
            retorno.push(<Option value={_id}>{nombre}</Option>))
          )
          return retorno
        }else{
          return(
              <Option>Sin resultados</Option>
          )
        }})
        .catch(console.log)
    }else if(empleados.length){
      return(
        <Select>
          { empleados.map(({_id,nombre}) => (
            <Option value={_id}>{nombre}</Option>
          ))}
        </Select>
      )
    }else{
      return(
        <Select>
          <Option>Sin resultados</Option>
        </Select>
      )
    }
  }
  
  return (
    <div>
      <Title style={titleStyle} level={4}>{empresa._id?"Editar empresa":"A√±adir empresa"}</Title>
      <Form {...layout} 
        form={form}
        onFinish={onFinish}
        initialValues={valoresInicio}
        onFinishFailed={onFinishFailed}
      >
        <Form.Item
          label="Nombre"
          name="nombre"
          rules={[{ required: true, message: 'Introduzca un nombre' }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="Select"
          name="responsable"
          hasFeedback
          rules={[{ required: true, message: 'Selecciona un responsable' }]}
        >
          <Select>
            {cargarSelect}
          </Select>
        </Form.Item>
        <Form.Item {...tailLayout}>
          <Space size="middle">
            <Button 
              type="primary" 
              htmlType="submit" 
              >
              {empresa._id?"Editar":"Crear"}
            </Button>
            <Divider/>
            <Button 
              type="primary" danger
              onClick={valoresInicio}
              >
              Cancelar
            </Button>	
          </Space>
        </Form.Item>
      </Form>
      <Link to="/empresas">‚Üê Volver a Empresas</Link>
    </div>
  );
}

export default Empresa